import React from 'react';
 import { Field, Formik } from 'formik';
import { useSelector } from 'react-redux';
import { getAllCars } from '../../redux/cars/selectors';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import { ButtonFilter, FilterItemContainer, FormContainer, InputMileage, Label, MilageContainer, OptionBrand, OptionPrice } from './FormikFilterAll.styled';

const inputBrand={
    fontSize:'18px',
    lineHeight:'1.33',
    fontWeight:500,
    fontFamily: 'Manrope, sans-serif',
    width: 224,
    height:49,
    paddingLeft:15,
    borderColor: 'rgba(138, 138, 137, 0.2)',
    backgroundColor: 'rgba(138, 138, 137, 0.2)',
    borderRadius: '8px',
    outline: 'none'
}

const inputPrice={
    fontSize:'18px',
    lineHeight:'1.33',
    fontWeight:500,
    fontFamily: 'Manrope, sans-serif',
    width: 125,
    height:49,
    paddingLeft:15,
    borderColor: 'rgba(138, 138, 137, 0.2)',
    backgroundColor: 'rgba(138, 138, 137, 0.2)',
    borderRadius: '8px',
    outline: 'none'
    }

const FormikFilterAll=({onSubmit})=>{

const cars=useSelector(getAllCars);
 const carsBrands=new Set( [...cars.map(item=>item.make)].sort((a,b)=>a.localeCompare(b)));
const carsBrandsArray=[...carsBrands];
const rentPrice=[30,40,50,60,70,80,90,100,110,120,130,140,150,160,170,180,190,200]

const handleSubmit=({brand, price, minMileage, maxMileage}, actions)=>{
    if( minMileage && maxMileage && minMileage>=maxMileage){
      Notify.failure('Min mileage cannot be greater than max mileage');
      return
    }
  const valuesToSend = {
        brand,
        price,
        minMileage,
        maxMileage,
      };
      console.log(brand, price, minMileage)
    onSubmit(valuesToSend)    
}

   return (
   <div>
     <Formik
       initialValues={{ brand: 'All brands', price: 'All', minMileage: '', maxMileage: '' }}
       onSubmit={ handleSubmit}
     >
         <FormContainer>
            <FilterItemContainer>
                <Label htmlFor="brand">Car brand</Label>
                <Field as="select" style={inputBrand} name="brand"  placeholder="Select a car brand" >
                        <OptionBrand >All brands</OptionBrand>
                        {carsBrandsArray?.map((item) => (
                                <OptionBrand key={item} value={item}>{item}</OptionBrand>
                                ))
                            }
                </Field>
            </FilterItemContainer>
           <FilterItemContainer>
                <Label htmlFor="price">Price/ 1 hour</Label>
                <Field as="select" style={inputPrice} name="price" >
                        <OptionPrice >All</OptionPrice>
                        {rentPrice?.map((item) => (
                                    <OptionPrice key={item} value={item}>{item}</OptionPrice>
                                    ))
                                }
                </Field>
           </FilterItemContainer>
           <FilterItemContainer>
                <Label htmlFor="mileage">Сar mileage / km</Label>
                <MilageContainer>
                        <InputMileage type="number"  id="minMileage" min="1" name="minMileage" placeholder="From" />
                        <InputMileage type="number" id="maxMileage" min="1" name="maxMileage" placeholder="To" />
                </MilageContainer>
           </FilterItemContainer>
           <ButtonFilter type="submit" >Search</ButtonFilter>
         </FormContainer>
     </Formik>
   </div>
 )
}

export default FormikFilterAll